export default {
  props: {
    imageHeader: {
      type: String,
      required: false,
    },
    images: {
      type: Array,
      required: false,
    },
  },
  data() {
    return {
      slide: 0,
      sliding: null,
    }
  },
  methods: {
    onSlideStart(slide) {
      this.sliding = true
    },
    onSlideEnd(slide) {
      this.sliding = false
    },
  },
}
